<template>
  <div class="tags-container" :class="size ? `is-${size}`: ''">
    <div class="tags-wrap flex-vertical-center">
      <el-tag
        v-for="tag in tags1"
        :key="tag"
        :closable="!disabled"
        :disable-transitions="false"
        @close="handleClose(tag)">
        <span class="tag-content" :class="{'is-closable' : !disabled}"
          :title="tag">{{tag}}</span>
      </el-tag>

      <div v-if="!inputVisible">
        <i v-if="!tags2.length && !disabled && tags.length < maxTagsLen" class="iconfont icon-add" @click="showInput" />

        <!-- 显示剩余的tags -->
        <el-popover v-else-if="tags2.length" placement="bottom" trigger="hover" width="400">
          <div class="tags-wrap flex-vertical-center flex-wrap"
            :class="[size ? `is-${size}`: '', popoverClass]">
            <el-tag
              v-for="tag in tags2"
              :key="tag"
              :closable="!disabled"
              :disable-transitions="false"
              @close="handleClose(tag)">
              <span class="tag-content" :class="{'is-closable' : !disabled}"
                :title="tag">{{tag}}</span>
            </el-tag>
            <template v-if="!disabled && tags.length < maxTagsLen">
              <i v-if="!innerInputVisible"
                class="iconfont icon-add"
                @click="showInnerInput" />
              <el-input
                v-else
                v-model="innerInputValue"
                class="input-new-tag"
                ref="innerInput"
                size="mini"
                :maxlength="maxlength"
                @keyup.enter.native="handleInputConfirm"
                @blur="handleInputConfirm" />
            </template>
          </div>
          <i slot="reference" class="iconfont icon-ellipsis" />
        </el-popover>
      </div>

      <el-input
        v-else
        v-model="inputValue"
        class="input-new-tag"
        ref="saveTagInput"
        size="mini"
        :maxlength="maxlength"
        @keyup.enter.native="handleInputConfirm"
        @blur="handleInputConfirm" />
    </div>

    <!-- 临时tag标签，用来计算标签的宽度 -->
    <div style="visibility: hidden;" :class="`tags-${className}`">
      <el-tag
        v-for="tag in tags"
        :key="tag"
        closable
        class="tag-item">
        {{tag}}
      </el-tag>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';

export default Vue.extend({
  props: {
    data: {
      type: Array,
      default() {
        return [];
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    // 最多输入的字符长度
    maxlength: {
      type: Number,
      default: 100,
    },
    // 最多输入的标签数
    maxTagsLen: {
      type: Number,
      default: 10,
    },

    size: {
      type: String,
      default: '',
    },

    tagColor: {
      type: String,
    },
    popoverClass: {
      type: String,
    },
  },
  data() {
    return {
      tags: this.data,
      tags1: [],
      tags2: [],
      inputVisible: false,
      innerInputVisible: false,
      inputValue: '',
      innerInputValue: '',
      className: '',
      visible: false,
    };
  },
  created() {
    this.className = new Date().getTime().toString();
  },
  mounted() {
    this._initTagsLayout();
  },
  watch: {
    data(value) {
      this.tags = value;
      this._initTagsLayout();
    },
  },
  methods: {
    handleClose(tag) {
      this.tags.splice(this.tags.indexOf(tag), 1);
      this.$nextTick(() => {
        this._initTagsLayout();
      });
      this.$emit('delete', tag);
      this.$emit('change', this.tags);
    },
    handleInputConfirm() {
      const tagName = this.inputValue || this.innerInputValue;
      if (tagName && !this.tags.includes(tagName)) {
        this.tags.push(tagName);
        this.$nextTick(() => {
          this._initTagsLayout();
        });
        this.$emit('create', tagName);
        this.$emit('change', this.tags);
      }
      this.inputVisible = false;
      this.innerInputVisible = false;
      this.inputValue = '';
      this.innerInputValue = '';
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    showInnerInput() {
      this.innerInputVisible = true;
      this.$nextTick(() => {
        this.$refs.innerInput.$refs.input.focus();
      });
    },
    _initTagsLayout() {
      const tags = document.querySelectorAll(`.tags-${this.className} .tag-item`);
      const visibleArea = document.querySelectorAll('.tags-container')[0];
      const visibleWidth = parseFloat(getComputedStyle(visibleArea).width);
      const inputWidth = this.disabled ? 0 : 100;
      const gapWidth = 10;
      let widthTotal = 0;
      let itemNum = 0;

      Array.from(tags).forEach((tag, i) => {
        widthTotal += parseFloat(getComputedStyle(tag).width, 10);
        if (widthTotal <= visibleWidth - inputWidth - (gapWidth * (i + 1))) {
          itemNum = i;
        } else {
          this.visible = true;
        }
      });

      this.tags1 = this.tags.slice(0, itemNum + 1);
      this.tags2 = this.tags.slice(itemNum + 1);
    },
  },
});
</script>

