


























































































































































import {
  defineComponent, ref, watch, computed, reactive, inject,
} from '@vue/composition-api';

import Tags from '@/components/base/TagsAuto.vue';
import axios from '@/api/axios';
import UnitField from '@/views/material/info/components/UnitField.vue';
import {
  getMaterialDetail,
  getMaterialCollectStatus,
  addCollect,
  removeCollect,
  increaseClick,
  getLibraryMaterialDetail,
} from '@/api/material-mall';
import open from '@/utils/open-window';
import MaterialColorViewer from '@/views/material/info/components/ColorViewer.vue';
import useLoader from '_/composables/use-loader';
import exec from '@/utils/exec';
import { stringifyUrl } from '@/utils/oss/utils/process';
import Navigator from '../components/Navigator.vue';
import { getCurrentCompany } from '../../../loaders/company';

import GoodsBaseInfo from './GoodsBaseInfo.vue';
// import GoodsCarousel from './GoodsCarousel.vue';
import MaterialCarousel from './MaterialCarousel.vue';
import CollectTip from './CollectTip.vue';


export default defineComponent({
  components: {
    Navigator,
    Tags,
    UnitField,
    GoodsBaseInfo,
    MaterialCarousel,
    MaterialColorViewer,
  },
  props: {
    id: {
      type: String,
      default: '',
    },
    fromLibrary: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { root, emit }) {
    const { $errHandler, $route, $router } = root;

    const company = computed(() => {
      return getCurrentCompany();
    });
    // 面料详细信息
    const material = ref<any>({
      samplePrice: 0,
      supplierArticleName: '',
    });

    // 面料商名，用于获取色卡板
    const supplierArticleName = ref<string>('');
    // 面料色卡板
    // const colorBoardList = ref<any>([]);

    // 收藏情况
    const collectStatus = ref<Boolean>(false);
    const initCollectStatus = ref<Boolean>(false);

    // 轮播图列表
    const swipers = ref<any>([]);

    // 获取轮播的需要的数据结构
    function _getSwiperObj(mat: any) {
      swipers.value = [];
      // 获取三张合成图
      const img = mat.imgJson;

      ['thumbnail', 'flat', 'texture', 'crease', 'toeThumbnail'].forEach(item => {
        if (item === 'texture' && img.texture && parseInt(mat.isCloudRenderComplete as string, 10) === 1) {
          swipers.value.push({
            type: 'image',
            url: stringifyUrl({
              url: img.flat,
              process: {
                image: {
                  crop: {
                    w: Math.floor(2000 / 5 * 2.54),
                    h: Math.floor(2000 / 5 * 2.54),
                    x: 0,
                    y: 0,
                  },
                },
              },
            }),
            isCloudRenderComplete: mat.isCloudRenderComplete,
            scale: mat.imgProperty?.[`${item}Scale`],
            imageType: item,
          });
          return;
        }
        if (img[item]) {
          swipers.value.push({
            type: 'image',
            url: img[item],
            imageType: item,
            isCloudRenderComplete: mat.isCloudRenderComplete,
            // 展示图片标尺，flatScale 和 textureScale
            scale: mat.imgProperty?.[`${item}Scale`],
          });
        }
      });

      // 获取附件的材质图片
      if (Array.isArray(img.images)) {
        // @ts-ignore
        swipers.value.push(...img.images?.map(item => {
          return { type: 'image', url: item };
        }));
      }

      // 获取附件的视频
      if (Array.isArray(img.videos)) {
        // @ts-ignore
        swipers.value.push(...img.videos?.map(item => {
          return { type: 'video', url: item };
        }));
      }

      //  获取附件的模型
      if (Array.isArray(img.videos)) {
        // @ts-ignore
        swipers.value.push(...img.models?.map(item => {
          return { type: 'model', url: item.cover, id: item.id };
        }));
      }
      return swipers.value;
    }

    // 获取商品详情;
    async function getMallMaterialInfoById() {
      try {
        const materialOrigin = (await getMaterialDetail(props.id)).data;
        // 处理单位类型的数据类型 number 转为 string; (否则编辑页面的表单校验会提示转为字符串类型)
        Object.keys(materialOrigin).forEach(key => {
          if (materialOrigin[key] === null) materialOrigin[key] = '';
          else if (typeof materialOrigin[key] === 'number') {
            materialOrigin[key] = materialOrigin[key].toString();
          }
          if (key === 'tags') {
            materialOrigin.tags = materialOrigin.tags.map((tag: any) => {
              if (typeof tag === 'string') {
                return { tag };
              }
              return tag;
            });
          }
        });
        supplierArticleName.value = materialOrigin.supplierArticleName;

        const setCollectStatus = async () => {
          collectStatus.value = (await getMaterialCollectStatus(props.id)).data;
          initCollectStatus.value = collectStatus.value;
        };

        material.value = materialOrigin;
        Promise.all([await setCollectStatus(), await increaseClick(props.id)]);
      } catch (err) {
        $errHandler(err);
      }
    }

    // 获取收藏夹详情
    async function getLaibraryMaterialInfoById() {
      try {
        const materialOrigin = (await getLibraryMaterialDetail(props.id)).data;
        // 处理单位类型的数据类型 number 转为 string; (否则编辑页面的表单校验会提示转为字符串类型)
        Object.keys(materialOrigin).forEach(key => {
          if (materialOrigin[key] === null) materialOrigin[key] = '';
          else if (typeof materialOrigin[key] === 'number') {
            materialOrigin[key] = materialOrigin[key].toString();
          }
          if (key === 'tags') {
            materialOrigin.tags.map((tag: any) => {
              if (typeof tag === 'string') {
                return { tag };
              }
              return tag;
            });
          }
        });

        supplierArticleName.value = materialOrigin.supplierArticleName;

        material.value = materialOrigin;

        collectStatus.value = true;
        initCollectStatus.value = true;
      } catch (err) {
        $errHandler(err);
      }
    }

    // 更变收藏情况
    async function changCollectStatus() {
      try {
        if (collectStatus.value) {
          await removeCollect(material.value.id, material.value.materialId);
          collectStatus.value = false;
        } else {
          if (!localStorage.getItem('finishCollectTip')) {
            root.$injectDialog({
              is: CollectTip,
            }, {
              props: {
                width: '460px',
              },
            }).open();
          }
          await addCollect(material.value.id, material.value.materialId);
          collectStatus.value = true;
        }
      } catch (e) {
        root.$errHandler(e);
      }
    }

    // 跳转到商家首页
    function goToCompany() {
      let companyPath = '';
      if ($route.path.startsWith('/qt/')) {
        companyPath = '/qt/supplier-page/preview/homepage';
      } else {
        companyPath = '/supplier-page/preview/homepage';
      }
      $router.push({
        path: companyPath,
        query: {
          companyId: material.value.companyId,
        },
      });
      emit('cancel');
    }

    const loading = ref(false);

    watch(() => props.id, async () => {
      if (!props.id) return;
      loading.value = true;
      try {
        if (props.fromLibrary || $route.path.startsWith('/qt/library')) {
          await getLaibraryMaterialInfoById();
        } else {
          await getMallMaterialInfoById();
        }

        _getSwiperObj(material.value);
      } catch (e) {
        $errHandler(e);
      } finally {
        loading.value = false;
      }
    }, {
      immediate: true,
    });

    const params = reactive({
      supplierArticleName,
      company,
    });

    const colorBoardList = useLoader(
      async ({
        company: companyInfo,
        supplierArticleName: articleName,
      }) => {
        // @ ts-ignore
        let res: any = null;
        if (!companyInfo || !articleName) return [];
        if (companyInfo.type === 3) {
          // 面料商获取商家色卡板
          res = (await axios.get('/mall/material/company/articles', {
            params: {
              articleName,
              limit: 10,
            },
          })).data;
        }
        // 获取商家色卡板
        res = (await axios.get('/mall/mall/colorSwatch/list', {
          params: {
            supplierArticleName: articleName,
            pageNumber: 1,
            pageSize: 10,
          },
        })).data.records;

        return res;
      },
      params,
      {
        initialValue: [],
      },
    );

    function goToGoodsDetail(id: number | string) {
      if (root.$route.path.startsWith('/qt/')) {
        root.$router.replace({
          query: {
            id: id.toString(),
          },
        });
      } else if (id !== material.value.id) {
        open({
          query: {
            id: id.toString(),
          },
        });
      }
    }

    const isQt = inject('isQt') as () => boolean;

    function goToShare(id: string) {
      root.$router.push({
        path: '/generate-material-image',
        query: {
          id,
        },
      });
    }

    return {
      company,
      material,
      swipers,
      collectStatus,
      colorBoardList,
      loading,

      goToCompany,
      goToGoodsDetail,
      changCollectStatus,
      goToShare,
    };
  },
  // beforeRouteEnter(to, from, next) {
  //   if (from.fullPath === '/') {
  //     next(vm => {
  //       vm.shouldBackToHome = true;
  //     });
  //   }
  //   next();
  // },
});
