





















































import { defineComponent, ref } from '@vue/composition-api';

import VideoPlayer from '@/components/video/index.vue';
import ModelPurePreviewer from '@/views/model/preview/ModelPurePreviewer.vue';
import { delay } from '@4dst-saas/public-utils/dist/delay';

export default defineComponent({
  name: 'CarouselMaterialImages',
  components: {
    VideoPlayer,
    ModelPurePreviewer,
  },
  props: {
    swiperList: {
      type: Array as () => { type: string, url: string, imageType: string, id: string, isCloudRenderComplete?: '0' | '1', scale: string }[],
      required: true,
    },
    materialId: {
      type: String,
      default: '',
    },
    size: {
      tyep: String,
      default: '',
    },
    initialIndex: {
      type: Number,
      default: 0,
    },
  },
  // @ts-ignore
  setup(props, { root: { $ImageViewer, $Dialog } }) {
    const carousel = ref(null);

    async function previewImage() {
      const imgList = props.swiperList.reduce((acc, cur) => {
        // @ts-ignore
        if (cur.type === 'image') acc.push(cur.url);
        return acc;
      }, []);
      await $ImageViewer.show(imgList as string[], {
        index: ((carousel.value) as any).activeIndex,
        targetElems: '.material-img',
      });
    }

    async function previewVideo(sourceUrl: string) {
      await $Dialog.dialogify({
        is: VideoPlayer,
        props: {
          sourceURL: sourceUrl,
        },
      }, {
        props: {
          fullscreen: true,
          width: '100%',
          height: '100%',
          top: '0',
          bottom: '0',
          center: true,
        },
      });
    }

    async function previewModel(id: string) {
      await $Dialog.dialogify({
        is: ModelPurePreviewer,
        props: {
          modelId: id,
          materialId: props.materialId,
          mode: 'modelMaterial',
        },
      }, {
        props: {
          fullscreen: true,
          width: '100%',
          height: '100%',
          top: '0',
          bottom: '0',
          flex: 'column',
          center: true,
        },
      });
    }

    // 设置轮播图的滚动的index(外部调用)
    function carouselSetActiveItem(index: number) {
      ((carousel.value) as any).setActiveItem(index);
    }

    const modelLoadedList = ref<number[]>([]);

    async function changeIndex(index: number) {
      if (props.swiperList[index].type === 'model') {
        await delay(500);
        modelLoadedList.value = [...modelLoadedList.value, index];
      }
    }

    function showRenderFlag(item: typeof props.swiperList[0]) {
      return item.imageType && ['thumbnail', 'flat', 'texture', 'crease', 'toeThumbnail'].includes(item.imageType)
        && parseInt(item.isCloudRenderComplete as string, 10) !== 1;
    }

    function showScale(item: typeof props.swiperList[0]) {
      // return item.imageType && (item.imageType === 'texture' || item.imageType === 'flat');
      return !!item.scale;
    }

    return {
      carousel, previewImage, previewVideo, previewModel, carouselSetActiveItem, changeIndex, modelLoadedList, showRenderFlag, showScale,
    };
  },
});
