






























import i18n from 'i18next';
import {
  defineComponent, ref, watch, computed,
} from '@vue/composition-api';
import CustomCollapse from '@/components/collapse/Collapse.vue';
import axios from '@/utils/axios';

import FromFieldText from '@/views/supplier-backend/components/FromFieldText.vue';
import translateCategoryName from '@/utils/i18n-material-type';

// 获取大中小类组合的类名
async function getCategoryName(id: string) {
  return ((await axios({
    method: 'post',
    url: '/show/category/name',
    data: {
      ids: [id],
    },
  })).data)[id];
}

export default defineComponent({
  components: {
    FromFieldText,
  },
  props: {
    material: {
      type: Object,
      require: true,
      default: () => ({}),
    },
  },
  setup(props) {
    const loading = ref(true);
    const fields = ref<any>([]);
    const collapseStatus = ref<Boolean>(false);


    watch(() => props.material, async (val, oldVal) => {
      // 当请求回material数据才渲染页面
      loading.value = !!oldVal;

      const categoryName = await getCategoryName(val.category) as string;
      const i18nCategoryName = categoryName.split('/').map(
        name => translateCategoryName(name),
      ).join('/');

      fields.value = [
        {
          label: i18n.t('material.info.material_type'),
          value: i18nCategoryName,
          // units: [
          //   { type: 'materialType', value: val.materialType },
          // ],
        },
        {
          label: i18n.t('material.info.materialsTechnology'),
          prop: 'materialsTechnology',
          value: val.materialsTechnology
            ? val.materialsTechnology.split(',').map((m: string) => i18n.t(`material.materialsTechnology.${m}`)).join() : '',
        },
        {
          label: i18n.t('material.info.surfaceTechnics'),
          prop: 'surfaceTechnics',
          value: val.surfaceTechnics
            ? val.surfaceTechnics.split(',').map((m: string) => i18n.t(`material.surfaceTechnics.${m}`)).join() : '',
        },
        {
          label: i18n.t('material.info.designContent'),
          prop: 'designContent',
          value: val.designContent
            ? val.designContent.split(',').map((m: string) => i18n.t(`material.designContent.${m}`)).join() : '',
        },
        {
          label: i18n.t('material.info.applicableProductLine'),
          prop: 'applicableProductLine',
          value: val.applicableProductLine
            ? val.applicableProductLine.split(',').map((m: string) => i18n.t(`material.applicableProductLine.${m}`)).join() : '',
        },
        // { label: '材料种类', prop: i18n.t(`material.materialsTechnology.${val.materialsTechnology}`) },
        // { label: '表面工艺', prop: i18n.t(`material.surfaceTechnics.${val.surfaceTechnics}`) },
        // { label: '图案内容', prop: i18n.t(`material.designContent.${val.designContent}`) },
        // { label: '适用产品线', prop: i18n.t(`material.applicableProductLine.${val.applicableProductLine}`) },
        {
          label: i18n.t('material.info.moq'),
          prop: 'moq',
          units: [
            { type: 'mqqUnit', value: val.moqUnit },
          ],
        },
        { label: i18n.t('material.info.season'), prop: 'season' },
        {
          label: i18n.t('material.info.sample_period'),
          prop: 'samplePeriod',
          units: [
            { type: 'date', value: val.samplePeriodUnit },
          ],
        },
        {
          label: i18n.t('material.info.lead_time'),
          prop: 'productionLeadTime',
          units: [
            { type: 'date', value: val.productionLeadTimeUnit },
          ],
        },
        { label: i18n.t('material.info.specification'), prop: 'specification' },
        {
          label: i18n.t('material.info.thickness'),
          prop: 'thickness',
          units: [
            { type: 'thicknessUnit', value: val.thicknessUnit },
          ],
        },
        {
          label: i18n.t('material.info.original_country'),
          units: [
            { type: 'country', value: val.materialCountryOfOrigin },
          ],
        },
      ];
    }, { deep: true, immediate: true });

    const printPatternsInfoTags = computed(() => {
      return props.material.printPatternsInfo?.map((tag: string | object) => {
        if (typeof tag === 'string') {
          // return { tag };
          return tag;
        }
        // return tag;
        return (tag as any).tag;
        // }) || [];
      }).join('、') || '';
    });
    const patternsTags = computed(() => {
      return props.material.patterns?.map((tag: string | object) => {
        if (typeof tag === 'string') {
          // return { tag };
          return tag;
        }
        // return tag;
        return (tag as any).tag;
        // }) || [];
      }).join('、') || '';
    });
    const tagsDisplay = computed(() => {
      return [printPatternsInfoTags.value, patternsTags.value].filter(e => e && e.length > 0).join('、');
    });

    return {
      collapseStatus,
      fields,
      loading,

      // printPatternsInfoTags,
      // patternsTags,
      tagsDisplay,
    };
  },
});
