<template>
  <div class="flex-center flex-column">
    <img src="@/assets/img/material-mart/collect-tip.png" width="300">
    <p style="color: #666" v-html="$t('material.info.collect_tip')">
    </p>
    <el-button type="primary" round @click="finishTip" style="margin-top: 20px">
      {{ $t('material.info.got_it') }}
    </el-button>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

export default defineComponent({
  setup(_, { emit }) {
    function finishTip() {
      localStorage.setItem('finishCollectTip', true);
      emit('cancel');
    }

    return {
      finishTip,
    };
  },
});
</script>

