import axios from './axios';

/**
 * 查询面料详细信息
 */
export const getMaterialDetail = (id) => {
  return axios({
    method: 'get',
    url: '/mall/material',
    params: {
      id,
    },
  });
};

/**
 * 读取收藏信息的接口
 */
export const getLibraryMaterialDetail = (id) => {
  return axios({
    method: 'get',
    url: '/mall/collect/info',
    params: {
      id,
    },
  });
};


/**
 * 获取是否收藏
 */
export const getMaterialCollectStatus = (id) => {
  return axios({
    method: 'post',
    url: '/mall/collect/check',
    data: {
      id,
    },
  });
};

/**
 * 新增收藏
 */
export const addCollect = (id, materialId) => {
  return axios({
    method: 'post',
    url: '/mall/collect/add',
    data: {
      id,
      materialId,
    },
  });
};
/**
 * 取消收藏
 */
export const removeCollect = (id, materialId) => {
  return axios({
    method: 'post',
    url: '/mall/collect/remove',
    data: {
      id,
      materialId,
    },
  });
};

/**
 * 取消收藏
 */
export const increaseClick = (id) => {
  return axios({
    method: 'post',
    url: '/mall/mall/click/increase',
    data: {
      id,
    },
  });
};
